@import url("https://fonts.googleapis.com/css2?family=Space+Grotesk:wght@500&display=swap");
:root {
  --LightViolet: hsl(249, 99%, 64%);
  --DarkViolet: hsl(278, 68%, 11%);
  --Violet: hsl(278, 94%, 30%);
  --Red: hsl(0, 100%, 66%);
  --White: hsl(0, 0%, 100%);
  --LightGray: hsl(270, 3%, 87%);
  --DarkGray: hsl(279, 6%, 55%);
  --Font: "Space Grotesk", sans-serif;
}

body,
html {
  font-family: var(--Font);
  font-size: 18px;
  margin: 0;
  padding: 0;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: var(--LightViolet);
}

main {
  width: 350px;
  height: 100%;
  display: flex;
  flex-direction: column;
}

@media screen and (min-width: 992px) {
  body,
  html {
    background-color: var(--Violet);
  }
  main {
    width: 800px;
    height: 500px;
    display: flex;
    flex-direction: row;
  }
}
section {
  width: 100%;
  height: 200px;
  background: url("../images/bg-main-mobile.png") no-repeat;
}
section :nth-child(1) {
  width: 205px;
  height: 110px;
  position: relative;
  top: 26%;
  right: -37%;
}
section :nth-child(2) {
  width: 205px;
  height: 110px;
  position: relative;
  top: 0;
  right: -4%;
}
section :nth-child(3) {
  width: 205px;
  height: 110px;
  position: relative;
  top: -57%;
  right: -4%;
}

@media screen and (min-width: 992px) {
  section {
    width: 30%;
    height: 100%;
    background: url("../images/bg-main-desktop.png") no-repeat;
  }
  section :nth-child(1) {
    width: 205px;
    height: 110px;
    position: relative;
    top: 50%;
    right: -50%;
  }
  section :nth-child(2) {
    width: 205px;
    height: 110px;
    position: relative;
    top: 0%;
    right: -25%;
  }
  section :nth-child(3) {
    width: 205px;
    height: 110px;
    position: relative;
    top: -23%;
    right: -25%;
  }
}

